@import 'Variables.scss';

.list{
    margin-top: 40px;
    @media (min-width: 768px){
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 10px;
    }
    @media (min-width: 1200px){
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}
.alternative{
    @include alternative;
}